.selectAndMultiselectHost {
  width: calc(50% - 20px);
  border: 1px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  min-height: 55px;
  font-family: var(--app-text-main-font), serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.selectAndMultiselectHostTiles {
  width: calc(50% - 20px);
  border: 0px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  min-height: 55px;
  font-family: var(--app-text-main-font), serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.section {
  display: flex;
  flex-direction: column;
}

.accordionContainer {
  overflow: hidden;
  margin: 0 0 20px 0;
  font-family: var(--app-text-main-font), serif;
  font-size: 16px;
  //font-weight: 500;
  background-color: #FFFFFF;
  color: var(--app-color-gray-dark);
  padding: 10px 20px 20px 20px;
  align-items: center;
  border: 1px dashed var(--app-color-gray-light);
  border-radius: 8px;
  outline: none;
  transition: background-color 0.6s ease;

  &[app-variant-has-error="error"] {
    border: 1px solid var(--app-color-error);
  }
}

.active {
  background-color: #FFFFFF;
}

.active .title {
  font-family: var(--app-text-main-font), serif;
}

.customTitleInput {
  border: 1px solid #D2D2D2;
  border-radius: 8px;
  padding-left: 10px;
  align-content: center;
  margin-top: 5px;
  margin-bottom: auto;
  height: 37px;
  font-family: var(--app-text-main-font), serif;
  font-size: 1rem;
  color: var(--app-color-gray-dark);

  &::placeholder {
    color: var(--app-color-gray-light);
  }
}

.icon {
  margin-left: 20px;
  margin-top: auto;
  margin-bottom: auto;
  transition: transform 0.6s ease;
}

.loaderContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 32px;
  max-height: 80%;
  margin: 0 0px 0 5px;

  .host[comp-loading="loading"] & {
    transform: translateX(-5px);
  }
}

.iconsContainer {
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
  cursor: pointer;
}

/* Style to rotate icon when state is active */
.rotate {
  transform: rotate(180deg);
}

/* Style the accordion content panel. Note: hidden by default */
.content {
  background-color: white;
  text-align: left;
  overflow: auto;
  transition: max-height 0.6s ease;
}

.fieldTypeInput {
  ::placeholder {
    font-family: var(--app-text-main-font), serif;
    font-size: 1rem;
    color: var(--app-color-error);
  }  
  width: inherit;
  padding-left: 10px;
  border: 0px;
  height: 40px;
  font-family: var(--app-text-main-font);
  font-size: 16px;
  font-weight: normal;
  color: var(--app-color-gray-dark);
  background-color: rgba(234, 231, 231, 0.91);
  
}

.fieldTypeTextarea {
  ::placeholder {
    font-family: var(--app-text-main-font), serif;
    font-size: 1rem;
    color: var(--app-color-error);
  }

  width: inherit;
  padding: 0 10px 0 10px;
  border: none;
  height: 100px;
  font-family: var(--app-text-main-font),
  serif;
  font-size: 16px;
  //font-weight: 700;
  color: var(--app-color-gray-dark);
  background-color: rgba(234, 231, 231, 0.91);
}
input {
  outline: none;
}

.fieldRowContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  margin-bottom: auto;
  height: 32px;
  flex: 1 1;
  
}

.accordion:hover,
.title {
  font-family: var(--app-text-main-font), serif;
  font-size: 16px;
  //font-weight: 600;
  letter-spacing: 0;
  line-height: 24px;
  text-align: left;
}

.emulatedFlexGap {
  padding-top: 10px;
  padding-bottom: 15px;
  display: inline-flex;
  flex-wrap: wrap;
  width: 100%;
}

.singleInputContainer {
  border: none;
  height: auto;
  margin-top: -20px;

  input {
    cursor: pointer;
    text-align: left;
    padding-left: 15px;
    font-family: var(--app-text-main-font), serif;
    color: #4A4A4A;
    font-size: 16px;
    //font-weight: 500;
  }
}

.inputContainerClassName {
  input {
    cursor: pointer;
    text-align: left;
    padding-left: 15px;
    font-family: var(--app-text-main-font), serif;
    color: #4A4A4A;
    font-size: 16px;
    //font-weight: 500;
  }
}

.phoneIInput {
  outline: none !important;
  width: 100% !important;
  border: none !important;
  height: 37px !important;
  padding-top: 0 !important;
  padding-left: 80px !important;
  padding-bottom: 0 !important;
}

.currencySelectContainer {
