.userCard {
    cursor: pointer;
    width: 100%;
    min-height: 100px;
    background-color: var(--app-color-white-lightest);
    border-radius: 8px;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
  }
  
  .cls.userCardContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  
  .userCardContainerTop {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 100%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
  
  .userCardInnerContainer {
    margin-top: 60px;
    height: 80px;
    width: 80px;
    background-color: var(--app-color-gray-lighter);
    border-radius: 50%;
  }
  .avatarUrlImg{
    height: 80px;
    width: 80px;
    border-radius: 50%;
  }
  .userCardBottomContainer {
    padding: 30px;
  }
  
  .fullName {
    font-family: var(--app-text-main-font);
    font-size: 24px;
    color: var(--app-color-gray-dark);
    text-align: center;
  }
  
  .position {
    font-family: var(--app-text-main-font);
    font-size: 1rem;
    color: var(--app-color-gray-light);
    text-align: center;
    margin-top: -10px;
  }

  .email {
    font-family: var(--app-text-main-font);
    font-size: 16px;
    font-weight: 700;
    color: var(--app-color-gray-light);
    text-align: center;
    margin-top: -20px;
  }